import Vue from "vue";
import Vuex from 'vuex';

import App from "./App.vue";

Vue.config.productionTip = false;
Vue.use(Vuex);

var store = new Vuex.Store({
  state: {
    playing:false
  },
  mutations: {
    play (state) {
      state.playing=!state.playing
    }
  }
})

new Vue({
	store: store,
  render: h => h(App)
}).$mount("#app");

