<template>
<audio ref="audioPlayer"><source :src="source"></audio>
</template>

<script>
export default {
	props:["source"],
	watch: {
  '$store.state.playing': function() {
    if(this.$store.state.playing)this.$refs.audioPlayer.play();
    else this.$refs.audioPlayer.pause();
  }
}
};
</script>

<style scoped>
audio{
	display:none;
}
</style>
