<template>
	<div class="radioInfo">
		<span>{{info}}</span>
		<span :class="statusIconClasses"></span>
	</div>
</template>

<script>
const axios = require('axios').default;
export default {
	props:["source"],
	computed: {
		statusIconClasses:function(){
			return {
				"statusIcon":true,
				"online":this.info === "Radio Online",
				"offline":this.info === "Radio Offline"
			}
		}
	},
	data: function() {
		return {
			info: "Conectando...",
			loading: true,
			errored: false,
			online: false
		}
	},
	mounted: function() {
    axios
	.get(this.source)
	.then(response => {
		if(response.data.activestreams===1)this.info = "Radio Online";
		else this.info = "Radio Offline";
	})
	.catch(() => {
		this.info = "Radio Offline";
		this.errored = true;
      })
	.finally(() => this.loading = false)
  }
};
</script>

<style>
.radioInfo{
  font-size: 1.4em;
  padding: 1em;
  text-shadow: 1px 1px 3px rgba(50,50,50,.8);
}
.statusIcon{
	width:.6em;
	height:.6em;
	border-radius: .6em;
	background-color: #777;
	display: inline-block;
	animation: iconBlink .5s linear infinite;
	margin-left: .4em;
}
.statusIcon.online{
	animation: none;
	background-color: #0f0;
	box-shadow: 0 0 .5em #0f0;
}
.statusIcon.offline{
	animation: none;
	background-color: #f00;
	box-shadow: 0 0 .5em #f00;
}
@keyframes iconBlink{
	0%{box-shadow: 0 0 .5em white;}
	50%{box-shadow: 0 0 0px white;}
	100%{box-shadow: 0 0 .5em white;}
}
</style>
